.social li{
  text-decoration: none;
  list-style: none;
  color: #fff;
}

.social {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  font-size: 0.8rem;
  font-weight: 400;
  padding: 0px 10px;

}


.copyright {
  font-size: 10px;
  font-weight: 200;
  color: #fff;
  align-self: center;
  text-align: center;
}

hr{
  margin-top: 1rem;
}

.line{
  width: 50%;
}

@media screen and (min-width: 900px) {
  .social {
    margin: 0px;
  }
}
